import y0_menu_switch from "url:../resources/sounds/y0_menu_switch.mp3";
import y0_menu_select from "url:../resources/sounds/y0_menu_select.mp3";
import y0_menu_discard from "url:../resources/sounds/y0_menu_discard.mp3";
import y0_upgrade_bought from "url:../resources/sounds/y0_upgrade_bought.mp3";

import rock_hit_0 from "url:../resources/sounds/rock_hit_0.mp3";
import rock_hit_1 from "url:../resources/sounds/rock_hit_1.mp3";
import {getRandomInt} from "../utils/math";

const soundMap = {
  y0_menu_switch: y0_menu_switch,
  y0_menu_select: y0_menu_select,
  y0_menu_discard: y0_menu_discard,
  y0_upgrade_bought: y0_upgrade_bought
};

const rockHitMap = [
  rock_hit_0, rock_hit_1
]

export default function (sound) {
  switch (sound) {
    case "rock_hit":
      if (sound === "rock_hit") {
        const audioFile = rockHitMap[getRandomInt(0, rockHitMap.length)];
        const playing = new Audio(audioFile);
        playing.volume = getRandomInt(5, 11) / 10;
        playing.play();
      }
      break;
    default:
      const audioFile = soundMap[sound];
      if (!audioFile) {
        console.log("Audio file not found!");
        return;
      }
      const playing = new Audio(audioFile);
      playing.play();
      break;
  }
}