import {getRandomInt} from "../utils/math";

import frame0 from '../resources/images/effects/dust-animation/frame_00.png';
import frame1 from '../resources/images/effects/dust-animation/frame_01.png';
import frame2 from '../resources/images/effects/dust-animation/frame_02.png';
import frame3 from '../resources/images/effects/dust-animation/frame_03.png';
import frame4 from '../resources/images/effects/dust-animation/frame_04.png';
import frame5 from '../resources/images/effects/dust-animation/frame_05.png';
import frame6 from '../resources/images/effects/dust-animation/frame_06.png';
import frame7 from '../resources/images/effects/dust-animation/frame_07.png';
import frame8 from '../resources/images/effects/dust-animation/frame_08.png';
import frame9 from '../resources/images/effects/dust-animation/frame_09.png';
import frame10 from '../resources/images/effects/dust-animation/frame_10.png';
import frame11 from '../resources/images/effects/dust-animation/frame_11.png';

const animationFrames = [frame0, frame1, frame2, frame3, frame4, frame5, frame6, frame7, frame8, frame9, frame10,
  frame11];

const framesDelay = 50;

export default function generateDustParticle(data) {
  const dust = document.createElement("div");
  dust.className = "dust-particle";
  const generatedY = data.y + getRandomInt(-10, 10) - 50; // effect size
  const generatedX = data.x + getRandomInt(-10, 10) - 50; // effect size
  const rotate = getRandomInt(-45, 45);
  const scale = getRandomInt(5, 15) / 10;

  dust.style.setProperty("top", generatedY + "px");
  dust.style.setProperty("left", generatedX + "px");
  dust.style.setProperty("transform", `scale(${scale}) rotate(${rotate}deg)`);

  document.body.appendChild(dust);

  const finalDelay = framesDelay + getRandomInt(-20, 20);

  for (let i = 0; i < animationFrames.length; i++) {
    setTimeout(() => {
      dust.style.setProperty("background-image", `url("${animationFrames[i]}")`);
    }, (i * finalDelay));
  }
  setTimeout(() => {
    dust.parentNode.removeChild(dust);
  }, animationFrames.length * finalDelay + 10000);
}