import {getRandomInt} from "../utils/math";

export default function generateIncText(data) {
  const text = document.createElement("div");
  text.className = "inc-text";
  text.innerText = `+${data.inc}`;
  const generatedY = data.y + getRandomInt(-10, 10);
  const generatedX = data.x + getRandomInt(-10, 10);
  text.style.setProperty("top", generatedY + "px");
  text.style.setProperty("left", generatedX + "px");
  document.body.appendChild(text);
  setTimeout(() => {
    text.classList.add("inc-text--flying-up");
  }, 0);
  setTimeout(() => {
    text.parentNode.removeChild(text);
  }, 3000);
}